import Vue from 'vue';
import Router from 'vue-router';
import Home from './views/Home.vue';
import About from './views/About.vue';
import Projects from './components/Projects/Projects.vue';
import ProjectPage from './components/Project-page/Project-page.vue';

Vue.use(Router);

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    { path: '/', component: Home },
    { path: '/about', component: About },
    { path: '/projects', component: Projects },
    { path: '/stock-trader', component: ProjectPage },
    { path: '/mi-music', component: ProjectPage },
    { path: '/mishka', component: ProjectPage },
    { path: '/sedona', component: ProjectPage },
    { path: '/nerds', component: ProjectPage },
    { path: '/barbershop', component: ProjectPage },
    { path: '/project', component: ProjectPage },
  ],
});
